'use client'; // Directiva necesaria en Next.js App Router para componentes con interactividad

import React, { useState, useEffect, ReactNode } from 'react';
import { supabase } from '@/lib/supabase';
import { toast } from '@/hooks/use-toast';
import { IconQuestionCircle, IconFileSignature, IconChalkboardTeacher, IconChartLine, IconFlagCheckered, IconFileInvoice, IconStarOfLife } from '@/components/educacion/ui/Icons';
import { Expediente, FormularioInfo, Familiar } from '@/types/educacion';
import DashboardView from '@/components/educacion/views/DashboardView';
import CaseView from '@/components/educacion/views/CaseView';
import FormView from '@/components/educacion/views/FormView';

// ===============================================================
// DATOS CONSTANTES
// ===============================================================
const allForms: FormularioInfo[] = [
    { id: 'form-entrevista-inicial', title: '1. Entrevista Inicial Educativa', icon: IconQuestionCircle, tableName: 'entrevistas' },
    { id: 'form-informe-inicial', title: '2. Informe Inicial Educativo', icon: IconFileSignature, tableName: 'informes_iniciales' },
    { id: 'form-entrevista-seguimiento', title: '3. Entrevista de Seguimiento', icon: IconChalkboardTeacher, tableName: 'entrevistas_seguimiento' },
    { id: 'form-informe-seguimiento', title: '4. Informe de Seguimiento', icon: IconChartLine, tableName: 'informes_seguimiento' },
    { id: 'form-entrevista-cierre', title: '5. Entrevista de Cierre', icon: IconFlagCheckered, tableName: 'entrevistas_cierre' },
    { id: 'form-informe-cierre', title: '6. Informe de Cierre', icon: IconFileInvoice, tableName: 'informes_cierre' },
    { id: 'form-informe-especial', title: '7. Informe Especial', icon: IconStarOfLife, tableName: 'informes_especiales' },
];

// ===============================================================
// COMPONENTE PRINCIPAL DE LA PÁGINA
// ===============================================================
export default function ModuloEducacionPage() {
    const [currentView, setCurrentView] = useState<'dashboard' | 'case' | 'form'>('dashboard');
    const [expedientes, setExpedientes] = useState<Expediente[]>([]);
    const [selectedExpediente, setSelectedExpediente] = useState<Expediente | null>(null);
    const [selectedForm, setSelectedForm] = useState<FormularioInfo | null>(null);
    const [isLoading, setIsLoading] = useState(true);
    const [isTestingConnection, setIsTestingConnection] = useState(false);

    const handleTestConnection = async () => {
        setIsTestingConnection(true);
        const { error } = await supabase.from('expedientes').select('*', { head: true });

        if (error) {
            toast({ 
                title: 'Error de Conexión', 
                description: error.message,
                variant: 'destructive',
            });
        } else {
            toast({ 
                title: 'Conexión Exitosa', 
                description: 'La conexión con Supabase se ha establecido correctamente.',
            });
        }
        setIsTestingConnection(false);
    };

    useEffect(() => {
        const fetchExpedientes = async () => {
            setIsLoading(true);
            // *** AQUÍ VA LA LLAMADA A SUPABASE PARA OBTENER LOS EXPEDIENTES ***
            // Ejemplo:
            // const { data, error } = await supabase.from('expedientes').select('*');
            // if (error) console.error('Error fetching expedientes:', error);
            // else setExpedientes(data || []);
            
            // Por ahora, iniciamos con una lista vacía hasta conectar Supabase.
            setExpedientes([]); 
            setIsLoading(false);
        };
        fetchExpedientes();
    }, []);

    const handleSelectExpediente = (expediente: Expediente) => {
        setSelectedExpediente(expediente);
        setCurrentView('case');
    };

    const handleSelectForm = (formInfo: FormularioInfo) => {
        setSelectedForm(formInfo);
        setCurrentView('form');
    };
    
    const handleCreateNew = () => {
        setSelectedExpediente(null);
        // Lleva directamente al primer formulario para crear un nuevo expediente
        handleSelectForm(allForms[0]); 
    };

    const navigateBack = () => {
        if (currentView === 'form') {
            setCurrentView(selectedExpediente ? 'case' : 'dashboard');
        } else if (currentView === 'case') {
            setCurrentView('dashboard');
        }
    };
    
    const handleSaveForm = async (formData: any) => {
        if (!selectedForm) return;

        console.log(`Simulando guardado en tabla: ${selectedForm.tableName}`, formData);
        alert('¡Datos guardados con éxito! (Simulación)');
        navigateBack();
    };

    const renderContent = () => {
        switch (currentView) {
            case 'case':
                return <CaseView expediente={selectedExpediente!} onSelectForm={handleSelectForm} onBack={navigateBack} allForms={allForms} />;
            case 'form':
                return <FormView formInfo={selectedForm!} expediente={selectedExpediente} onSave={handleSaveForm} onBack={navigateBack} />;
            default:
                return <DashboardView expedientes={expedientes} onSelectExpediente={handleSelectExpediente} onCreateNew={handleCreateNew} isLoading={isLoading} onTestConnection={handleTestConnection} isTestingConnection={isTestingConnection} />;
        }
    };

    return <div className="p-4 md:p-8 max-w-7xl mx-auto">{renderContent()}</div>;
}